import { Component, OnInit, Inject } from '@angular/core';
import { Router } from '@angular/router';

/* Services */
import { TokenService } from '../../../Services/Authentication/token.service';
import { SharedService } from '../../../Services/shared.service'
import { AuthenticationService } from '../../../Services/Authentication/auth-index';
import { AccountService } from '../../../Services/account.service'

/* Animations */


@Component({
	selector: 'app-auth-login',
	templateUrl: './auth-login.component.html',
	styleUrls: [
		'./auth-login.component.scss',
		'../../../../public/styles/login-signup-modal.scss'
	]
})
export class AuthLoginComponent implements OnInit {

	
	form 		: any = {}
	textButton	: string
	log_err 	: boolean = false
	showReset 	: boolean = false
	email_err 	: boolean = false
	pw_err 		: boolean = false
	resetText	: string
	viewHeight 	: number
	innerHeight : number
    innerWidth 	: number
    visible 	: boolean = true
    textValidateReset : string = ''

    validateForm(form) {
		const reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

		if (this.showReset) {
			this.textValidateReset = !reg.test(form.rMail) ? "Address must be valid" : ""
			return ({ case: 1, email_err: this.email_err, pw_err: this.pw_err, rMail_err: this.textValidateReset });
		} else {
			this.email_err = this.pw_err = true

			if (form.email)
				this.email_err = !reg.test(form.email)
			if (form.password)
				this.pw_err = form.password.length >= 8 ? false : true
			return ({ case: 2, email_err: this.email_err, pw_err: this.pw_err, rMail_err: this.textValidateReset });
		}
	}

	constructor(private SharedService: SharedService,
				private AuthService: AuthenticationService,
				private AccountService: AccountService,
				private TokenService: TokenService,
				private router: Router) {

		if ((window.screen.width) < 736) {
		}
	}

	getToken() {
		if (this.TokenService.getToken()) 
			this.router.navigate(['/welcome'])
	}

	ngOnInit() {
		this.resetText 		= "Forgot Password?"
		this.textButton 	= "Log In"
		this.form = {
			email 		: '',
			password 	: '',
			rMail 		: ''
		}
	}

	switchS() {
		setTimeout( () => this.SharedService.setSwitch('Login'), 300);
		this.visible = false
	}

	showResetPassword() {
		this.textButton 	= this.textButton === "Log In" ? "Reset Password" : "Log In"
		this.resetText 		= this.resetText === "Cancel" ? "Forgot Password?" : "Cancel"
		this.showReset 		= this.showReset === true ? false : true
		this.form.email 	= null
		this.form.password 	= null
		this.email_err 		= false
		this.pw_err 		= false
		this.log_err 		= false
	}

	process(form) {
		this.log_err = false
		const result = this.validateForm(form);

		if (result.case === 1 && !result.rMail_err) {
			let body = { email : form.rMail };
			this.AccountService.passwordRecoveryEmail(body).subscribe( res => {
				if (res.success)
					this.resetPasswordSent();
			}, error => {
				if (JSON.parse(error._body).error === 'Invalid email')
					this.textValidateReset = 'Email address not found'
			});
		} else if (result.case === 2 && !result.email_err && !result.pw_err) {
			delete form.rMail

			this.AuthService.localLogIn(form).subscribe( res => {
				this.refresh();
	        }, err => {
	            this.log_err = true
	        });
		}
	}

	resetPasswordSent() {
		this.textButton = 'Sent'
		this.form.rMail = ''

		setTimeout( () => {
		}, 1000);
	}

	refresh(): void {
		this.router.navigate(['/welcome']);
	}
}



