/* Modules */
import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/* Components */
import { AuthLoginComponent } from '../Components/authentications/auth-login/auth-login.component';
// import { AuthRegisterComponent } from '../Components/authentications/auth-register/auth-register.component';

/* Services */
import { AuthenticationService } from '../Services/Authentication/authentication.service';

/* Librairies */

const routes: Routes = [
	{ path: 'login', component: AuthLoginComponent },
	// { path: 'register', component: AuthRegisterComponent }
];

export const AUTH_ROUTES: ModuleWithProviders = RouterModule.forChild(routes);


